@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');

body {
  background-color:#29293d;
  text-align: center;
  font-family: Raleway;
  fill: #f2ffcc
}

.overlay {
  opacity: 0;
}

.title {
  fill: #f2ffcc;
  font-size: 24px;
}

#line-total {
  stroke: #ffb74d;
}

#tooltip {
  color: #331a00;
  background-color:#a3a3c2;
  border: 1px solid #7575a3;
  border-radius: 3px;
  height:40px;
  width:100px;
  font-size: 14px;
}

.line-label {
  fill: '#f2ffcc';
  font-size: 11
}

.axes-label {
  font-size: 18px;
}

#x-axis, #y-axis {
  color: #f2ffcc;
  font-size: 12px;
}


